@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500&display=swap);
.btn:focus {
    box-shadow: none !important;
}

a#basic-nav-dropdown, a.nav-link {
    color: black !important;
}
.button {
    color: white;
    background-color: #ff5722;
    border-color: #ff5722;
    font-size: 15px;
    font-weight: 500;
    line-height: 18px;
    border: 1px solid transparent;
}

.button:hover {
    background-color: #ff7043;
    border-color: #ff6838;
    transition: 0.2s;
}

.email {
    border-radius: 9999px;
    width: 60%;
    border-color: rgba(128, 138, 142, 0.2)
}

.iput:focus {
    outline: none;
}
.purple-container {
    background-color: rgb(106, 38, 218);
    padding-top: 80px;
    padding-bottom: 64px;
}

.tab-content {
    align-self: start;
    width: 100%;
    margin-left: 50px;
}

.p {
    color: rgba(255, 255, 255, 0.7);
    font-size: 16px;
    font-weight: 400;
    line-height: 28px;
}

.circles {
    width: 69px;
    height: 69px;
    margin-right: 30px;
    border-radius: 50%;
}

.c1 {
    background-color: rgb(255, 141, 82);
    margin-bottom: 10px;
}

.c2 {
    background-color: rgb(21, 205, 168);
}

.nav-link {
    font-size: 18px;
    color: rgba(255, 255, 255, 0.7) !important;
}

.nav-link:hover {
    border: transparent !important;
}

.nav-link.active {
    color: rgb(255, 209, 102) !important;
    background-color: transparent !important;
    border: transparent !important;
    border-bottom: 1px solid rgb(255, 209, 102) !important;
}
.gs-link {
    color: rgb(255, 87, 34);
    font-size: 18px;
    font-weight: 400;
    text-decoration: none;
    padding: 10px 0px;
    border-bottom: 2px solid rgb(255, 87, 34);
}

.gs-link:hover {
    color: #ff5722
}
.download-btn {
    background-image: url(data:image/png;base64,);
    padding: 120px;
    width: 45%;
    border-radius: 0 1rem 1rem 0;
    margin-left: 176px;
}

.button2 {
    background-color: white !important;
    border-color: white;
    font-size: 15px;
    font-weight: 500;
    border: 1px solid transparent;
    margin-right: 50px;
}
.footer-nav {
    color: rgb(59, 70, 86);
    opacity: 0.4;
    margin-bottom: 30px;
}

.f-link {
    text-decoration: none;
    color: rgb(37, 55, 63);
}

.f-link:hover {
    color: rgb(253, 52, 110)
}

.ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    padding-left: 65px;
}

.icon {
    color: #E73B54;
}

.grey-color {
    color: rgba(38, 39, 41, 0.7)
}

.footer {
    color: rgba(37, 55, 63, 0.7);
    font-size: 13px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 22px;
}
.heading {
    color: rgb(37, 55, 63);
    font-size: 70px;
    letter-spacing: -2px;
    font-weight: 500;
    line-height: 70px;
}

.statement {
    color: rgba(37, 55, 63, 0.7);
    font-size: 21px;
    font-weight: 400
}

@media screen and (max-width: 1400px) {
    .iput {
        width: 62%;
    }
}

@media screen and (max-width: 1315px) {
    .dispnon {
        display: none;
    }
    .disp {
        display: block
    };
}

@media screen and (max-width: 1270px) {
    .letss {
        padding-right: 500px;
    }
    .padlr {
        padding-left: 8rem;
    }
}

@media screen and (max-width: 1100px) {
    .disnon {
        display: none;
    }
}

@media screen and (max-width: 1180px) {
    .nav {
        width: 100% !important;
    }
}

@media screen and (max-width: 1035px) {
    .letss {
        padding-right: 250px;
    }
}

@media screen and (max-width: 990px) {
    .heading {
        text-align: center;
    }
    .statement {
        text-align: center;
    }
    .gslink-center {
        display: flex;
        justify-content: center;
    }
    .l-img {
        margin-left: 60px;
    }
    .r-content {
        width: 90%;
        padding-right: 60px;
    }
}

@media screen and (max-width: 770px) {
    .heading {
        font-size: 50px;
    }
    .heading-trio {
        font-size: 50px !important;
    }
    .statement {
        font-size: 18px;
    }
    .email {
        width: 100%;
    }
    .pad {
        padding-top: 0;
    }
    .purple-box {
        padding-left: 0;
        padding-right: 0;
    }
    .r-headings {
        font-size: 38px;
        padding-left: 3rem;
    }
    .padt {
        padding-top: 0;
    }
    .letss {
        padding-right: 0;
    }
    .gs-heading {
        font-size: 35px !important;
    }
    .bluefoot {
        max-width: 98% !important;
    }
    .gs-width {
        width: 20% !important;
    }
    .img {
        width: 350px !important;
        height: 275px !important;
    }
}

@media screen and (max-width: 555px) {
    .gs-heading {
        display: none;
    }
    .download-btn {
        padding-left: 0;
        padding-right: 0;
        margin-top: 0;
    }
}
body {
    font-family: 'Rubik', sans-serif;
}


